<script>
import book from "./book.vue";
import Codes from "@/utils/codes";
import clipboard from "@/utils/clipboard";
import logo from "@/assets/logo.png";
import { debounce } from "debounce";
import { mapGetters } from "vuex";

export default {
  name: "Main",
  components: {
    book,
  },
  data() {
    return {
      isShareDialogVisible: false,
      shareUrl: "",
      logo,
      term: "",
      viewMode: "list",
      scrollFixHeight: 400,
      filterType: "all",
      shouldShowMyCollection: location.hash !== "#/",
    };
  },
  computed: {
    ...mapGetters(["variationCount"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      },
    },
    hasStoredStatus() {
      return this.$store.state.hasStoredStatus;
    },
  },
  watch: {
    $route(to, from) {
      this.shouldShowMyCollection = location.hash !== "#/";
    },
  },
  mounted() {
    window.addEventListener("resize", this.onResize);
    window.addEventListener("scroll", this.scrollFix);

    const hash = this.$route.params.hash;
    if (hash) {
      this.$store.commit("READ_STATUS", hash);
    } else {
      this.$store.commit("LOAD_STATUS");
    }
    this.onResize();
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFix);
    window.removeEventListener("resize", this.onResize);
  },
  methods: {
    onResize() {
      if (window.innerWidth <= 512) {
        variationCount.classList.add("variation-count-fix");
      } else {
        variationCount.classList.remove("variation-count-fix");
      }
    },
    scrollFix(event) {
      // fix toolNav
      var top = window.scrollY,
        left = window.scrollX;
      var toolNav = document.getElementById("toolNav");
      var variationCount = document.getElementById("variationCount");
      if (top > this.scrollFixHeight) {
        toolNav.classList.add("fix-top");
        if (window.innerWidth > 512) {
          variationCount.classList.add("variation-count-fix");
        }
      } else {
        toolNav.classList.remove("fix-top");
        if (window.innerWidth > 512) {
          variationCount.classList.remove("variation-count-fix");
        }
      }
    },
    handleClipboard(text, event) {
      clipboard(text, event);
    },
    save() {
      this.$store.commit(
        "SAVE_STATUS",
        Codes.generateHash(this.status.join(""))
      );

      this.$message({
        showClose: true,
        message: "儲存成功，僅於此瀏覽器能夠讀取",
        type: "success",
      });
    },
    onShareClick() {
      Codes.generateURL(this.status.join(""), (url) => {
        this.isShareDialogVisible = true;
        this.shareUrl = url;
      });
    },
    onViewModeChange(data) {
      this.viewMode = data;
    },

    onFilterTypeChange(data) {
      this.$store.commit("UPDATE_FILTER_TYPE", data);
    },
    debounceInput: debounce(function(e) {
      console.log(this.term);
      this.$store.commit("UPDATE_TERM", this.term);
    }, 500),
  },
};
</script>

