<template>
  <div class="grid-container">
    <template v-for="(item, index) in filterListDatas">
      <div class="ad-card" :key="'ad-cards-'+index" v-if="index%100==0 && index!=0">
        <div>
          <InFeedAdsense
            data-ad-layout-key="-fg+5n+6t-e7+r"
            data-ad-client="ca-pub-1227876441837237"
            :data-ad-slot="'ad-cards-'+index"
            data-full-width-responsive="true"
          ></InFeedAdsense>
        </div>
      </div>

      <div class="card" :key="index">
        <div class="name">{{ item["zhName"] }}</div>
        <div class="sub-name">{{ item.name }}</div>
        <img
          class="main-variation"
          v-lazy="mainImage ? mainImage : item.content.image"
          v-if="false"
        />
        <div class="variations" v-if="item.variations.length > 0">
          <div class="variation-container" v-for="(obj, i) in item.variations" :key="i">
            <img
              class="variation"
              :class="{ 'variation--isOwnd': status[obj.idx] }"
              v-lazy="obj.content.image"
              @click="onVariationClick(obj.idx)"
              @mouseover="onVariationHover(obj.content.image)"
            />
            <span v-if="item.variations.length > 1" class="tooltip">
              {{
              obj.content.bodyColor_zh
              }}
            </span>
          </div>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "Cards",
  props: {
    onVariationClick: {
      type: Function,
      required: true
    },
    onVariationHover: {
      type: Function,
      required: true
    }
  },
  computed: {
    ...mapGetters(["filterListDatas"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    }
  },
  data() {
    return {
      mainImage: ""
    };
  },

  mounted() {
    setTimeout(function() {
      (adsbygoogle = window.adsbygoogle || []).push({});
    }, 2000);
  },
  methods: {}
};
</script>

