<template>
  <div class="wrapper">
    <!-- <transition name="fade" mode="out-in"> -->
    <div v-show="viewMode === 'list'" key="list">
      <lists :onVariationClick="toggleVariation"></lists>
    </div>

    <div v-show="viewMode === 'grid'" key="grid">
      <cards :onVariationClick="toggleVariation" :onVariationHover="modifyMainImage" />
    </div>
    <!-- </transition> -->
  </div>
</template>

<script>
import cards from "./cards.vue";
import lists from "./lists.vue";
import { mapGetters } from "vuex";

export default {
  name: "Book",
  props: {
    viewMode: {
      type: String,
      default: "list"
    },
    filterType: {
      type: String,
      default: "all"
    }
  },
  components: {
    cards,
    lists
  },
  data() {
    return {
      isShareDialogVisible: false,
      shareUrl: ""
    };
  },
  computed: {
    ...mapGetters(["filterListDatas"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    }
  },
  mounted() {},
  methods: {
    toggleVariation(order) {
      const currentStatus = this.status[order];
      this.status[order] = currentStatus ? 0 : 1;
      this.$store.commit("UPDATE_STATUS", this.status);
    },
    modifyMainImage(imgSrc) {
      this.mainImage = imgSrc;
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
.wrapper {
  background-color: #fff;
}
</style>

