<script>
import { mapGetters } from "vuex";

export default {
  name: "Lists",
  props: {
    onVariationClick: {
      type: Function,
      required: true
    }
  },
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["filterListDatas"]),
    status: {
      get() {
        return this.$store.state.status;
      },
      set(value) {
        this.$store.commit("UPDATE_STATUS", value);
      }
    }
  },
  mounted() {
    setTimeout(function() {
      (adsbygoogle = window.adsbygoogle || []).push({});
    }, 2000);
  },
  methods: {}
};
</script>

